<template>
<div>
    <!-- head start -->
    <div class="head" style="margin:6px 0 0 10px;height: 65px;line-height:65px;">
        <p style="font-size:28px;margin:0;">-</p>
    </div>
    <!-- head end -->

    <!--  -->
    <div style="margin-top:6px;">
        <el-form :label-position="labelPosition" label-width="120px" style="margin-top:6px;">
          <el-form-item label="">
            <el-date-picker v-model="value1" size="small"  type="date" placeholder="" :picker-options="pickerOptions0"></el-date-picker>
          </el-form-item>
          <el-form-item label="">
            <el-date-picker v-model="value2" size="small"  type="date" placeholder="" :picker-options="pickerOptions0"></el-date-picker>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="form.region1" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="form.region2" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="form.region3" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <el-form :label-position="labelPosition" label-width="120px" style="margin-top:6px;">
          <el-form-item label="">
            <el-input type="" name=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input type="" name=""></el-input>
          </el-form-item>
          <el-form-item label="">
           <el-select v-model="form.region4" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="form.region5" placeholder="">
              <el-option label="123" value="123"></el-option>
              <el-option label="456" value="456"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="form.region6" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
        </el-form>
    </div>

    <!--  -->
    <div style="margin-top:6px;">
      <el-table :data="tableData3" border style="width: 100%;border-top:0;font-size:12px;border-top: 1px solid #dfe6ec;" height="250">
          <el-table-column prop="date" label="" type="index" width="150"></el-table-column>
          <el-table-column prop="name" label="" ></el-table-column>
          <el-table-column prop="name" label="" ></el-table-column>
          <el-table-column prop="name" label="" ></el-table-column>
          <el-table-column prop="name" label="" ></el-table-column>
          <el-table-column prop="name" label="" ></el-table-column>
          <el-table-column prop="name" label="" ></el-table-column>
          <el-table-column prop="name" label="" ></el-table-column>
          <el-table-column prop="name" label="" ></el-table-column>
          <el-table-column prop="name" label="" ></el-table-column>
          <el-table-column prop="name" label="" ></el-table-column>
          <el-table-column prop="name" label="" ></el-table-column>
          <el-table-column prop="name" label="" ></el-table-column>
          <el-table-column prop="name" label="" ></el-table-column>
      </el-table>
    </div>


</div>
</template>

<script> 20
export default {
    data() {
        return {
            elPosition: 'right',
            formLabelAlign: {
              name: '',
              region: '',
              type: ''
            },
            value1:'',
            value2:'',
            labelPosition:'center',
            pickerOptions0: {
              disabledDate(time) {
                return time.getTime() < Date.now() - 8.64e7;
              }
            },
            form: {
              name: '',
              region1: '',
              region2: '',
              region3: '',
              region4: '',
              region5: '',
              region6: '',
              date1: '',
              date2: '',
              delivery: false,
              type: [],
              resource: '',
              desc: ''
            },
            tableData3: [
                {
                  date: '',
                  name: '',
                  province: '',
                  city: '',
                  address: '',
                  zip: 1
                },
                {
                  date: '',
                  name: '',
                  province: '',
                  city: '',
                  address: '',
                  zip: 2
                },
                {
                  date: '',
                  name: '',
                  province: '',
                  city: '',
                  address: '',
                  zip: 3
                },
                {
                  date: '',
                  name: '',
                  province: '',
                  city: '',
                  address: '',
                  zip: 4
                }
            ]
        }
    },
    methods: {
        getSummaries(param) {
            const { columns, data } = param;
            const sums = [];
            columns.forEach((column, index) => {
              if (index === 0) {
                sums[index] = '';
                return;
              }
              const values = data.map(item => Number(item[column.property]));
              if (!values.every(value => isNaN(value))) {
                sums[index] = values.reduce((prev, curr) => {
                  const value = Number(curr);
                  if (!isNaN(value)) {
                    return prev + curr;
                  } else {
                    return prev;
                  }
                }, 0);
                sums[index] += ' ';
              } else {
                sums[index] = '-';
              }
            });
            return sums;
        }
    }
}
</script>


<style scoped>
.el-form-item {
    display: inline-block;
    margin-bottom: 0;
}
</style>


