import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'
import * as getters from './getters'

Vue.use(Vuex)//

// 
const state = {
    count: 10
}

//  mutations
const mutations = {
    INCREMENT(state,n) {
            state.count+=10
    },
    DECREMENT(state,n) {
        state.count-=5
    }
}

//  store 
export default new Vuex.Store({
    actions,
    getters,
    state,
    mutations
})


