<template>
    <div>
    <Table size="large" :columns="columns1" :data="data1"></Table>
    <Table size="small" :columns="columns1" :data="data1"></Table>
    </div>
</template>
<script>
    export default {
        data () {
            return {
                columns1: [
                    {
                        title: '',
                        key: 'name'
                    },
                    {
                        title: '',
                        key: 'age'
                    },
                    {
                        title: '',
                        key: 'address'
                    }
                ],
                data1: [
                    {
                        name: '',
                        age: 18,
                        address: ''
                    },
                    {
                        name: '',
                        age: 25,
                        address: ''
                    },
                    {
                        name: '',
                        age: 30,
                        address: ''
                    },
                    {
                        name: '',
                        age: 26,
                        address: ''
                    }
                ]
            }
        }
    }
</script>


