                      <el-option label="" value=""></el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item label="">
                    <el-select v-model="formInline.region6" placeholder="">
                      <el-option label="" value=""></el-option>
                      <el-option label="" value=""></el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item label="" prop="sex">
                    <el-radio-group v-model="addForm.sex">
                      <el-radio class="radio" :label="1"></el-radio>
                      <el-radio class="radio" :label="0"></el-radio>
                    </el-radio-group>
                  </el-form-item>
                  <el-form-item label="">
                    <el-date-picker v-model="value2" size="small" style="width:157px;" type="date" placeholder="" :picker-options="pickerOptions0">
                    </el-date-picker>
                  </el-form-item>
                  <el-form-item label="">
                    <el-date-picker type="date" placeholder="" v-model="addForm.birth"></el-date-picker>
                  </el-form-item>
                  <el-form-item label="">
                    <el-select v-model="formInline.region8" placeholder="">
                      <el-option label="" value=""></el-option>
                      <el-option label="" value=""></el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item label="">
                    <el-select v-model="formInline.region9" placeholder="">
                      <el-option label="" value=""></el-option>
                      <el-option label="" value=""></el-option>
                    </el-select>
                  </el-form-item>

                  <el-form-item label="">
                    <el-select v-model="formInline.region10" placeholder="">
                      <el-option label="" value=""></el-option>
                      <el-option label="" value=""></el-option>
                    </el-select>
                  </el-form-item>

                  <el-form-item label="" v-model="addForm.trainer">
                    <el-input type="" name=""></el-input>
                  </el-form-item>
                  <el-form-item label="" v-model="addForm.liveaddr">
                    <el-input style="width:419px;height:25px;">
                    </el-input>
                  </el-form-item>
                  <el-form-item label="" v-model="addForm.addr">
                    <el-input style="width:419px;height:25px;">
                    </el-input>
                  </el-form-item>
                </el-form>
            </el-col>
            <el-col :span="24" class="infotitle">
              <strong></strong>
            </el-col>
            <el-col :span="24" style="width: 100%;margin:6px 0;">
              <el-form :label-position="labelPosition" label-width="100px" :model="addForm" :rules="addFormRules">
                <el-form-item label="" v-model="addForm.mobile">
                  <el-input type="" name=""></el-input>
                </el-form-item>
                <el-form-item label="E-mail" v-model="addForm.email">
                  <el-input type="" name=""></el-input>
                </el-form-item>
                <el-form-item label="QQ" v-model="addForm.qq">
                  <el-input type="" name=""></el-input>
                </el-form-item>
                <el-form-item label="" v-model="addForm.friend">
                  <el-input type="" name=""></el-input>
                </el-form-item>
