    </section>
</template>

<script>
    import util from '../../common/js/util'
    //import NProgress from 'nprogress'
    import { getUserListPage, removeUser, batchRemoveUser, editUser, addUser } from '../../api/api';

    export default {
        data() {
            return {
                filters: {
                    name: ''
                },
                value1:'',
                value2:'',
                formInline: {
                  user: '',
                  region1: '',
                  region2: '',
                  region3: '',
                  region4: '',
                  region5: '',
                  region6: '',
                  region7: '',
                  region8: '',
                  region9: '',
                  region10: ''
                },
                users: [],
                total: 0,
                page: 1,
                listLoading: false,
                sels: [],//

                editFormVisible: false,//
                editLoading: false,
                editFormRules: {
                    name: [
                        { required: true, message: '', trigger: 'blur' }
                    ]
                },
                //
                editForm: {
                    id: 0,
                    name: '',
                    sex: -1,
                    age: 0,
                    birth: '',
                    addr: ''
                },

                addFormVisible: false,//
                addLoading: false,
                addFormRules: {
                    name: [
                        { required: true, message: '', trigger: 'blur' }
                    ]
                },
                //
                addForm: {
                    name: '',
                    sex: -1,
                    age: 0,
                    birth: '',
                    addr: '',
                    // mobile:'',
                    // email:'',
                    // qq:'',
                    // friend:'',
                    // Contac:''
                }

            }
        },
        methods: {
            //
            formatSex: function (row, column) {
                return row.sex == 1 ? '' : row.sex == 0 ? '' : '';
            },
            handleCurrentChange(val) {
                this.page = val;
                this.getUsers();
            },
            //
            getUsers() {
                let para = {
                    page: this.page,
                    name: this.filters.name
                };
                this.listLoading = true;
                //NProgress.start();
                getUserListPage(para).then((res) => {
                    this.total = res.data.total;
                    this.users = res.data.users;
                    this.listLoading = false;
                    //NProgress.done();
                });
            },
            //
            handleDel: function (index, row) {
                this.$confirm('?', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    //NProgress.start();
                    let para = { id: row.id };
                    removeUser(para).then((res) => {
                        this.listLoading = false;
                        //NProgress.done();
                        this.$message({
                            message: '',
                            type: 'success'
                        });
                        this.getUsers();
                    });
                }).catch(() => {

                });
            },
            //
            handleEdit: function (index, row) {
                this.editFormVisible = true;
                this.editForm = Object.assign({}, row);
            },
            //
            handleAdd: function () {
                this.addFormVisible = true;
                this.addForm = {
                    name: '',
                    sex: -1,
                    age: 0,
                    birth: '',
                    addr: ''
                };
            },
            //
            editSubmit: function () {
                this.$refs.editForm.validate((valid) => {
                    if (valid) {
                        this.$confirm('', '', {}).then(() => {
                            this.editLoading = true;
                            //NProgress.start();
                            let para = Object.assign({}, this.editForm);
                            para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
                            editUser(para).then((res) => {
                                this.editLoading = false;
                                //NProgress.done();
                                this.$message({
                                    message: '',
                                    type: 'success'
                                });
                                this.$refs['editForm'].resetFields();
                                this.editFormVisible = false;
                                this.getUsers();
                            });
                        });
                    }
                });
            },
            //
            addSubmit: function () {
                this.$refs.addForm.validate((valid) => {
                    if (valid) {
                        this.$confirm('', '', {}).then(() => {
                            this.addLoading = true;
                            //NProgress.start();
                            let para = Object.assign({}, this.addForm);
                            para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
                            addUser(para).then((res) => {
                                this.addLoading = false;
                                //NProgress.done();
                                this.$message({
                                    message: '',
                                    type: 'success'
                                });
                                this.$refs['addForm'].resetFields();
                                this.addFormVisible = false;
                                this.getUsers();
                            });
                        });
                    }
                });
            },
            selsChange: function (sels) {
                this.sels = sels;
            },
            //
            batchRemove: function () {
                var ids = this.sels.map(item => item.id).toString();
                this.$confirm('', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    //NProgress.start();
                    let para = { ids: ids };
                    batchRemoveUser(para).then((res) => {
                        this.listLoading = false;
                        //NProgress.done();
                        this.$message({
                            message: '',
                            type: 'success'
                        });
                        this.getUsers();
                    });
                }).catch(() => {

                });
            },
            handleMouseDown(event, column){
                console.log(this.$isServer);
                this.dragging = true;
            }
        },
        mounted() {
            this.getUsers();
        }
    }

</script>

<style scoped>
 #head{
   width: 100%;
 }
 #head .el-button {
  border: 1px solid #959595;
  padding-left: 10px;
  width: 80px;
  height: 26px;
  margin-left: 0;
  position: relative;
  vertical-align:middle;
  background:#eaeaea;
  cursor: pointer;
  border-radius:0;
  font-family: arial, sans-serif;
  font-size: 13px;
  font-weight: normal;
  color: #000000;
 }
#head img{
  display:block;
  width: 14px;
  height: 14px;
  position: absolute;
  top:5px;
  left:15px;
 }
.title_ {
   height: 34px;
   background: #eef1f6;
   line-height: 34px;
   border-left: 5px solid #e57715;
   font-size: 13px;
   font-weight: 800;
   padding-left:3px;
}
.el-form-item {
    display: inline-block;
}
.toolbar {
    background-color: #fff;
}
/* Dialog */
.el-dialog {
    padding: 0px 20px 0;
    background: #f2f2f2;
    height: 32px;
    line-height: 32px;
}
.el-dialog__header {
  background: red;
}
.el-dialog__header {
  padding: 20px 20px 0;
}
.el-dialog__body {
  padding: 4px 20px;
}
#isusing {
  width: 15px;
  height: 15px;
  vertical-align: middle;
}
strong {
  margin-right: 10px;
  color: #87b87f !important;
}
.infotitle {
  border-bottom: 1px solid #87b87f;
}
.el-form-item__error {
    width: 100%;
    top: 8px;
    left: 160px;
}
.el-loading-mask {
    transition: opacity .01s;
}
</style>


